import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingPageComponent } from './clients/merchant/landing-page/landing-page.component';
import { Page404Component } from './clients/merchant/page404/page404.component';
import { RedflagComponent } from './clients/debtor/redflag/redflag.component';

const routes: Routes = [{
    path: '',
    redirectTo: 'debtor/login',
    pathMatch: 'full',
  },
  {
    path: 'debtor',
    loadChildren: () => import('./clients/debtor/debtor.module').then(m => m.DebtorModule)
  },
  {
    path: 'merchant',
    loadChildren: () => import('./clients/merchant/merchant.module').then(m => m.MerchantModule)
  },
  {
    path: 'admin',
    loadChildren: () => import('./clients/admin/admin.module').then(m => m.AdminModule)
  },
  {
    path: 'url',
    component: LandingPageComponent
  },
  {
    path: '404',
    component: Page404Component
  },
  {
    path: 'redflag',
    component: RedflagComponent,

  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
